import React from "react";
import { useDraggable } from "@dnd-kit/draggable";
export interface GenericBlockData {
  id: number;
  x: number;
  y: number;
  title: string;
  label: string;
}
type GenericBlockProps = {
  block: GenericBlockData;
};

export const GenericBlock = ({ block }: GenericBlockProps) => {
  //just dnd stuff
  const { attributes, listeners, setNodeRef, transform } = useDraggable({
    id: block.id,
  });
  if (!block.title) {
    block.title = "Empty title";
  }
  return (
    <div className="p-4 border w-[200px] m-1 border-black-200 rounded-xl">
      <h1>{block.title}</h1>
      <small>
        x: {block.x}, y: {block.y}
      </small>
      <p>{block.label}</p>
    </div>
  );
};
